@import './variables.scss';

.navigation-section {
  display: flex;
  flex-direction: column;
}

.navigation-full-width-wrapper {
  background: $gray-light;
  min-width: 320px;
}

.nav-bar {
  display: flex;
  justify-content: space-between;  
  max-width: 480px;
  min-width: 320px;
  margin: 0 auto 0.3rem auto;

  &.card {
    padding: 0.7rem 0;
  }

  .lnr {
    font-size: 2rem;
  }
}

.nav-button {
  border: none;
  background: none;
  text-align: center;
  user-select: none
}

.user-photo {
  height: 2rem;
  border-radius: 100%;
}

.date-selector {
  background: $white;

  .date {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    font-weight: 600;
    user-select: none;
  }

  .lnr {
    font-size: 1.3rem;
  }

  .nav-button {
    padding: 0.6rem 0;
  }
}
@media only screen and (max-width: 700px) {
  .navigation-section {
    position: fixed;
    width: 100%;
    bottom: 0;
    flex-direction: column-reverse;
    z-index: 2
  }

  .date-selector {
    width: 100%;

    .lnr {
      font-size: 1rem;
    }

    .nav-button {
      padding: 0;
    }
  }

  .nav-bar {
    justify-content: space-around;
    margin: 0 auto;
    &.card {
      padding: 0.7rem;
      box-sizing: border-box;
    }
  }
}